@import './variables';

@media screen and (max-width: 479px){
    .landing-container{
        &#hero section{
            padding: 40px 24px;
            gap: 40px;
            .container{
                &-1 .info{
                    .title{
                        margin-bottom: 16px;
                        &-1{@include set-text(body, small-semibold);}
                        &-2{@include set-text(display, 2);}
                    }
                    .description{
                        gap: 8px;
                        .group {gap: 20px;}
                        .bullet{
                            .label{
                                &-1{@include set-text(body, small-semibold);}
                                &-2{@include set-text(body, caption-small);}
                            }
                            &:after{
                                top: 8px;
                                width: 4px;
                                height: 4px;
                            }
                        }
                        .group .bullet:not(:first-child):after{
                            width: 4px;
                            height: 4px;
                            left: -12px;
                        }
                    }
                }
                &-2 .countdown-timer{
                    gap: 20px;
                    .timer-block{
                        width: 60px;
                        &-number{@include set-text(display, 2);}
                    }
                }
            }
        }

        &#showcase .showcase{
            &-item{
                padding: 80px 24px 40px;
                .container{
                    &-1{
                        //
                    }
                    &-2 .add .list{
                        display: grid;
                        grid-gap: 8px 20px;
                        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
                        grid-auto-rows: 1fr;
                    }
                }
            }
            &-link{
                padding: 80px 24px;
            }
        }

        &#about section{
            padding: 80px 24px;

            .container-1{
                //
                .card{
                    &#stress-reduction{
                        left: -3%;
                        top: 12%;
                        width: 165px;
                    }
                    &#cm-health{
                        right: -7%;
                        top: 32%;
                        width: 150px;
                    }
                    &#enhanced-cfunction{
                        left: -7%;
                        top: 58%;
                        width: 166px;
                    }
                }
                &:after{
                    bottom: 12px;
                }
            }
        }
    }
}

