.landing-container{
    // styles.scss
    &#hero{
        position: relative;
        background-color: get-color(main, primary-variant);

        &:before{
            position: absolute;
            content: "";
            z-index: 0;
            width: 66%;
            top: 0;
            left: 0;
            bottom: 0;

            background-image: radial-gradient(#A3FFBD 2px, transparent 2px), radial-gradient(#A3FFBD 2px, transparent 2px);
            background-size: 64px 64px;
            background-position: 0 0,32px 32px;
        }
        &:after{
            position: absolute;
            content: "";
            z-index: 0;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-image: linear-gradient(rgba(0,0,0,0.5), transparent 100px, get-color(main, primary) 90%);
        }

        .container{
            &-1{
                z-index: 1;
                .info{
                    position: relative;
                    display: flex;
                    justify-content: center;
                    width: 100%;
                    // z-index: 1;
        
                    .title{
                        display: flex;
                        flex-direction: column;
                        color: get-color(main, on-secondary);
                        text-align: center;
                        flex: 1;
                        
                        &-1{text-shadow: 0 0 4px get-color(main, primary-container-2);}
                        &-2{text-shadow: 0 0 5px get-color(main, primary-container-1);}
                    }

                    .description{
                        &, .group{
                            display: flex;
                            color: get-color(neutral, 10);
                            .bullet{
                                position: relative;
                                display: flex;
                                flex-direction: column;
                                text-align: center;
                                flex: 1;
                                span{
                                    color: get-color(main, secondary-variant);
                                    text-shadow: 0 0 8px get-color(main, secondary);
                                }
    
                                &:after{
                                    position: absolute;
                                    top: 8px;
                                    border-radius: 50%;
                                    background-color: get-color(neutral, 100);
                                    box-shadow: 0 0 22px 4px red;
                                }
                            }
                        }
                    }
                    // &:before{
                    //     // background-color: #182939;
                    //     content: "";
                    //     position: absolute;
                    //     z-index: -1;
                    //     bottom: 0;
                    //     left: -10px;
                    //     height: 80px;
                    //     width: 320px;
                    //     background-image: linear-gradient(to right, #0D171F, #0D171F 18px, transparent 18px, transparent);
                    //     background-size: calc(100% / 8) 100%;
                    //     transform: skewX(-18deg);
                    // }
                    // &:after{
                    //     content: "";
                    //     position: absolute;
                    //     z-index: -1;
                    //     top: -60px;
                    //     right: 40px;
                    //     height: 240px;
                    //     width: 240px;

                    //     // background-color: #182939;
                    //     // opacity: 0.6;
                    //     background-image: radial-gradient(#1B7C5D 4px, transparent 4px);
                    //     background-size: calc(100% / 5) calc(100% / 5);
                    //     transform: rotate(2deg);
                    // }
                }
            }
            &-2{
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 100%;
                z-index: 1;

                .countdown{
                    &-heading{color: get-color(neutral, 50);}
                    &-timer{
                        display: flex;
                        justify-content: center;
                        width: 100%;
                        .timer-block{
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            flex: 1;
                            & > *{
                                text-align: center;
                                width: 100%;
                                color: get-color(neutral, 50);
                            }
                            &-number{text-shadow: 0 0 7px get-color(main, secondary);}
                        }
                    }
                }
            }
        }
    }

    &#showcase{
        padding: 0;
        background-color: get-color(neutral, 10);
        section.showcase{
            &-item{
                display: flex;
                background-color: get-color(neutral, 50);
                .info{
                    color: get-color(main, secondary);
                    .description{color: get-color(main, on-secondary-container);}
                }
                .add{
                    display: flex;
                    flex-direction: column;
                    border-top: 2px solid get-color(main, primary-container-1);
                    color: get-color(main, primary-variant);
                    .list{
                        .item{
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            border-radius: 21px;
                            transition: all .3s ease;
                            cursor: default;
                            position: relative;
                            flex: 1;

                            .label{text-align: center;}
                            &:not(:first-child):after{
                                content: "";
                                position: absolute;
                                background-color: get-color(main, primary-container-2);
                                top: 0;
                                border-radius: 4px;
                            }
                        }
                    } 
                }
            }
    
            &-link{
                display: flex;
                justify-content: center;
                align-items: center;
                // width: 100%;
                border-radius: 40px 40px 0 0;
                box-shadow: 0 -8px 20px 0 rgba(0,0,0,0.25);
                color: get-color(main, primary);
                svg, path{fill: get-color(main, primary-variant);}
            }
        }
    }

    &#about{
        background: linear-gradient(to bottom, get-color(neutral, 10) 2%,get-color(main, secondary-variant) 100%);
        
        section{
            display: flex;
            .container{
                &-1{
                    position: relative;
                    svg{
                        fill: get-color(main, secondary-hover);
                        z-index: 1;
                    }
                    .card{
                        z-index: 2;
                        position: absolute;
                        display: flex;
                        color: get-color(main, primary-variant);
                        backdrop-filter: blur(5px);
                        box-shadow: 0 0 50px 2px rgba(0,0,0,0.2);
                        background-color: rgba(252, 252, 252, 0.3);
                        border-radius: 8px;
    
                        svg{
                            fill: get-color(main, primary-variant);
                        }
                    }
                    &:after{
                        content: "";
                        position: absolute;
                        background: get-color(main, primary-variant);
                        border-radius: 50%;
                        opacity: 80%;
                    }
                }
                &-2{
                    color: get-color(main, primary);
                }
            }
        }
    }
}