@media screen and (max-width: 479px){
    .stage-container{
        gap: 20px;
        padding: 8px 0 24px;
        li{
            margin-left: 28px;
            padding-left: 32px;
        }
    }
}

@media screen and (min-width: 480px){
    .stage-container{
        gap: 24px;
        padding: 16px 0 24px;
        li{
            margin-left: 36px;
            margin-right: 24px;
            padding-left: 40px;
        }
    }
}

@media screen and (max-width: 943px){
    .stage-container{
        li{
            @include set-text(display, 2-small);
            
            &:before{
                width: 36px;
            }
        }
        .label{
            margin: 4px 0;
        }
        .description{@include set-text(body, small);}
    }
}

@media screen and (min-width: 944px){
    .stage-container{
        gap: 40px;
        padding: 24px 0 80px;
        li{
            @include set-text(display, 2);
            margin-left: 62px;
            margin-right: 40px;
            padding-left: 48px;
            
            &:before{
                width: 40px;
            }
        }
        .label{
            margin: 8px 0;
        }
        .description{@include set-text(body, large);}
    }
}

ol.stage-container{
    list-style-type: none;
    li{
        position: relative;
        counter-increment: li;
        
        &:before{
            position: absolute;
            left: -16px;
            content: counter(li);
            padding: 6px 0;
            border-radius: 50%;
            text-align: center;
            background-color: get-color(main, tertiary-container);
        }
    }
}