@import './variables';

@media screen and (max-width: 479px){
    .header,.modal-container{
        padding-left: 24px;
        padding-right: 24px;
    }
}

@media screen and (min-width: 480px){
    .header,.modal-container{
        padding-left: 40px;
        padding-right: 40px;
    }
}

@media screen and (max-width: 712px){
    .btn-icon#nav-menu{
        display: block;
    }

    .header{
        padding-top: 0;
        padding-bottom: 0;
        .main-nav{
            display: none !important;
        }
    }
}

@media screen and (min-width: 713px){
    .btn-icon#nav-menu{
        display: none !important;
    }

    .header{
        padding: 4px 80px;

        .main-nav{
            display: flex !important;
        }
    }
}

@media screen and (max-width: 943px){
    .header{
        .main-nav{
            gap: 32px;
            a.btn-menuitem{
                @include set-text(header, menu-item-small);
            }
            ul{gap: 24px;}
        }
        &-logo{
            padding: 8px 0;
            svg{
                width: 95px;
                height: 32px;
            }
        }
    }

    a.btn-menuitem{
        @include set-text(header, menu-item-small);
        padding: 0;
    }
    .btn-icon{
        &#ig-link{
            width: 36px;
            padding: 9px 8px;
            svg{
                width: 18px;
                height: 18px;
            }
            i.fa{
                font-size: 18px;
                line-height: 18px
            }
        }
        &#nav-menu{
            padding: 10px;
            svg{
                width: 18px;
                height: 18px;
            }
        }
    }
}

@media screen and (min-width: 944px){
    .header{
        padding: 4px 120px;
        .main-nav{
            gap: 64px;
            ul{gap: 40px;}
        }
        &-logo{
            padding: 12px 0;
            svg{
                width: 131px;
                height: 44px;
            }
        }
    }
    a.btn-menuitem{
        @include set-text(header, menu-item-large);
        padding: 6px 0 2px;
    }
    .btn-icon{
        padding: 12px;
        &#ig-link{
            width: 48px;
            //
        }
        &#nav-menu{
            padding: 11px 12px;
            svg{
                width: 20px;
                height: 20px;
            }
        }
    }
}

@media screen and (min-width: 1280px){
    .header{
        padding: 0 160px;
    }
}

@media screen and (max-width: 345px){
    .header{
        width: 346px;
    }
}

@media screen and (min-width: 1441px){
    .header{
        width: 1440px;
        margin: 0 auto;
    }
}

@media (hover: hover), (-ms-high-contrast:none) and (pointer: fine) {
    .header-logo:hover svg path{
        fill: get-color(main, tertiary-container);
    }
    a.btn-menuitem:hover{
        color: get-color(main, tertiary);
        border-bottom-color: get-color(main, tertiary-container);
    }
    .header-nav, .main-nav{
        .btn-icon{
            &#nav-menu:hover{
                background-color: get-color(main, primary-variant-hover);
                svg,i.fa{
                    fill: get-color(main, on-primary);
                    color: get-color(main, on-primary);
                }
            }
            &#ig-link:hover{
                background-color: get-color(main, primary-variant-hover);
                outline-color: get-color(main, primary-container-1);
                svg, i.fa{
                    fill: get-color(main, tertiary-container);
                    color: get-color(main, tertiary-container);
                }
            }
        }
    }
}

a.btn-menuitem{
    background: none;
    border-bottom: 2px solid get-color(main, secondary-container);
    color: get-color(neutral, 100);
    border-radius: 0;
    text-decoration: none;

    &:active{
        color: get-color(main, tertiary);
        border-bottom-color: get-color(main, tertiary-container);
    }
}

.header-nav, .main-nav{
    .btn-icon{
        svg, i.fa{
            fill: get-color(neutral, 100);
            color: get-color(neutral, 100);
        }
    
        &#ig-link{
            outline-color: get-color(main, secondary-container);
            &:active{
                background-color: get-color(main, primary-variant-hover);
                outline-color: get-color(main, primary-container-1);
                svg, i.fa{
                    fill: get-color(main, tertiary-container);
                    color: get-color(main, tertiary-container);
                }
            }
        }
    
        &#nav-menu{
            position: relative;
            outline: none;
            svg, i.fa{
                transform: rotate(0);
                transition: transform .6s, opacity .4s;
            }
            /*svg.close{
                position: absolute;
                left: calc(50% - 9px);
            }
            .rotate{
                visibility: hidden;
                opacity: 0;
                transform: rotate(0);
                transition: transform .9s, opacity .4s;
            }
            .rotate-reset{
                visibility: visible;
                opacity: 1;
                transform: rotate(-180deg);
                transition: transform .6s, opacity .4s;
            }*/
            &:active{
                background-color: get-color(main, primary-variant-hover);
            }
            &.active i.fa{
                transform: rotate(180deg);
            }
        }
    }
}

.header{
    display: flex;
    justify-content: space-between;
    transition: box-shadow .6s, background-color .3s ease;
    background-color: get-color(main, primary-variant);

    &.home{
        background-color: transparent;
        // .header-nav, .header-logo{border-bottom: 1px solid rgba(0,0,0,0.3);}
    }
    &.drop, &.active{box-shadow: 0 2px 10px 0 rgba(0,0,0,0.4);}
    &-container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 99;
    }
    &-nav{
        width: 100%;
        justify-content: flex-end;
        &, .main-nav{
            display: flex;
            align-items: center;
            ul{
                list-style-type: none;
                display: flex;
            }
        }
    }
    &-logo{
        svg path{
            filter: drop-shadow(0 0 30px get-color(main, secondary));
            transition: fill .3s ease;
            fill: get-color(main, secondary-container);
        }
        &:active svg path{
            fill: get-color(main, tertiary-container);
        }
    }
}

.modal-container{
    padding-top: 16px;
    padding-bottom: 24px;
    position: fixed;
    z-index: 98;
    right: 2px;
    background-color: get-color(main, primary-variant);
    box-shadow: none;
    transform: translateY(-500px);
    transition: transform .5s, opacity .3s ease, box-shadow .3s ease;

    &.open{
        // display: block;
        opacity: 1;
        box-shadow: 0 2px 10px 1px rgba(0,0,0,0.4);
        transform: translateY(0);
    }

    .main-nav{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        gap: 24px;

        ul{
            list-style: none;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 16px;
        }
    }
}