@import './variables';
@import './header-nav'; // .btn-icon{#nav-menu, #ig-link}, .btn-menuitem
@import './home';
@import './menu';
@import './about';
@import './nprogress.css';

#nprogress .bar{
    background: get-color(main, secondary);
}
.header-container.loading{
    background-color: get-color(main, primary-variant);
}

body main, .footer-container{
    transition: opacity 0.4s;
    &.loading{opacity: 0;}
}

@media screen and (max-width: 479px){
    .page{
        &-title,&-body{
            padding-left: 24px;
            padding-right: 24px;
        }
        &-body{
            gap: 40px;
        }
    }

    .footer{
        gap: 8px;
        padding: 40px 0 24px;
        &-logo svg{
            height: 64px;
        }
    }
}

@media screen and (min-width: 480px){
    .page{
        &-title,&-body{
            padding-left: 40px;
            padding-right: 40px;
        }
        &-body{
            gap: 80px;
        }
    }

    .footer{
        padding: 64px 0 40px;
        &-logo svg{
            height: 80px;
        }
    }
}

@media screen and (max-width: 712px){
    //
}

@media screen and (min-width: 713px){
    .page-title h1, .page-body .menu-category, .section-heading, .section-body p, figcaption, .restrict{max-width: 581px;}
    .page{
        &-title,&-body{
            padding-left: 80px;
            padding-right: 80px;
        }
    }
}

