@media screen and (max-width: 1279px){
    .page-title h1, .page-body .menu-category, .section-heading, .section-body p, figcaption, .restrict{width: 100%;}
}

@media screen and (min-width: 1280px){
    .page-title h1, .page-body .menu-category, .section-heading, .section-body p, figcaption, .restrict{
        width: calc(8 * (100% + 40px) / 12 - 40px);
        max-width: none;
    }
    .page{
        &-title,&-body{
            padding-left: 160px;
            padding-right: 160px;
        }
    }
}

@media screen and (max-width: 345px){
    .header-container, .landing-container, .footer-container{
        width: 346px;
    }
}

@media screen and (min-width: 1441px){
    .page{
        width: 1440px;
        margin: 0 auto;
    }
}

*, :after, :before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    // scroll-behavior: smooth;
}

body{
    overflow-x: hidden;
    a{
        color: get-color(main, secondary);
        transition: color .2s ease;
    }
    .icon-padding{height: fit-content;}
}

@media (hover: hover), (-ms-high-contrast:none) and (pointer: fine) {
    body{
        a:hover{color: get-color(main, secondary-hover);}
    }

    .btn-primary:hover{
        background: get-color(main, secondary-hover);
        color: get-color(main, on-secondary);
    }

    .btn-secondary:hover{
        outline-color: get-color(main, primary-container-1);
        background-color: get-color(neutral, 10);
        color: get-color(main, secondary);
    }

    .btn-icon:hover{
        background-color: get-color(main, secondary-container);
        svg{fill: get-color(main, tertiary);}
    }
}

figure{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    line-height: 0;
    height: fit-content;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &.graphic img{
        background-color: get-color(main, secondary-container);
        border-radius: 5px;
    }
}

%btn{
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    border: none;
    border-radius: 4px;
    cursor:pointer;
    transition: background-color .2s ease, color .2s, outline .2s;
    height: fit-content;
    width: fit-content;
}
.btn-primary{
    @extend %btn;
    gap: 8px;
    background: get-color(main, secondary);
    color: get-color(main, on-secondary);
    // box-shadow: 0 10px 18px -8px rgba(16, 43, 96, 0.45);
    text-decoration: none;

    svg{fill: get-color(main, on-secondary);}
    &:active{
        background: get-color(main, secondary-hover);
        color: get-color(main, on-secondary);
    }
}
.btn-secondary{
    @extend %btn;
    gap: 8px;
    outline: 1px solid get-color(main, primary-container-2);
    outline-offset: -1px;
    background-color: get-color(neutral, 50);
    color: get-color(main, on-secondary-container);
    text-decoration: none;

    svg{fill: get-color(main, secondary);}
    &:active{
        outline-color: get-color(main, primary-container-1);
        background-color: get-color(neutral, 10);
        color: get-color(main, secondary);
    }
}

.btn-icon{
    @extend %btn;
    line-height: 0;
    outline: 1px solid get-color(main, primary-container-2);
    outline-offset: -1px;
    line-height: 0;
    height: fit-content;
    border-radius: 100px;
    background: none;
    border: none;
    text-decoration: none;
    svg{fill: get-color(main, on-primary);}

    &:active{
        background-color: get-color(main, secondary-container);
        svg{fill: get-color(main, tertiary);}
    }
}

.btn-menuitem{@extend %btn;}

.landing, .page{
    display: flex;
    flex-direction: column;
    align-items: center;

    .page{
        &-title, &-body, &-body .section, &-body .section-body{
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
        }
        &-title{
            // text-align: center;
            color: get-color(main, on-primary);
            background-color: get-color(main, primary-variant);
        }
        &-body{
            color: get-color(main, primary);
            background-color: get-color(neutral, 10);
        }
    }

    ol,ul{
        display: flex;
        flex-direction: column;
    }

    .info{
        &, .title{
            display: flex;
            flex-direction: column;
        }
        .title{
            &-1, &-2, .description{width: 100%;}
        }
    }
}

.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: get-color(main, secondary);

    &-container{
        background-color: get-color(main, primary-variant-container);
    }
    &-logo svg{
        width: auto;
        path{fill: get-color(main, secondary);}
    }
}