@import './variables';

@media screen and (min-width: 713px){
    .page#menu{
        .menu-category{
            // max-width: 
        }
    }
}

@media screen and (max-width: 943px){
    .page#menu{
        .page-body{gap: 40px;}
        .menu-category{
            gap: 4px;
            &-heading{
                // @include set-text(heading, 1-small);
            }
            &-list{
                gap: 24px;
                .list-item{
                    &-name{
                        @include set-text(body, small-semibold);
                        i.fa, span.tag{
                            margin-left: 4px;
                            &:first-child{margin-left: 8px;}
                        }
                        i.fa{
                            font-size: 13px;
                            line-height: 13px;
                        }
                        span.tag{
                            @include set-text(body, caption-small);
                        }
                    }
                    &-info{
                        gap: 24px;
                        .description{
                            @include set-text(body, caption-small-semibold);
                            padding-top: 4px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 944px){
    .page#menu{
        .page-body{gap: 80px;}
        .menu-category{
            gap: 8px;
            &-heading{
                // @include set-text(heading, 1);
            }
            &-list{
                gap: 32px;
                .list-item{
                    &-name{
                        @include set-text(body, large-semibold);
                        i.fa, span.tag{
                            margin-left: 6px;
                            &:first-child{margin-left: 10px;}
                        }
                        i.fa{
                            font-size: 16px;
                            line-height: 16px;
                        }
                        span.tag{
                            @include set-text(body, caption);
                        }
                    }
                    &-info{
                        gap: 40px;
                        .description{
                            @include set-text(body, caption-semibold);
                            padding-top: 4px;
                        }
                    }
                }
            }
        }
    }
}

@media (hover: hover), (-ms-high-contrast:none) and (pointer: fine) {
    //
}

.page#menu{
    // position: relative;

    .page{
        &-body{
            color: get-color(main, secondary);
            background-color: get-color(main, secondary-container);
        }
    }

    .menu-category{
        display: flex;
        flex-direction: column;
        // width: 100%;
        &-heading{
            //
        }
        &-list{
            display: flex;
            flex-direction: column;
            .list-item{
                list-style: none;
                &-name span.tag{
                    background-color: get-color(main, secondary);
                    color: get-color(main, on-secondary);
                    padding: 4px 4px 2px;
                    border-radius: 4px;
                }
                &-info{
                    display: flex;
                    justify-content: space-between;
                }
            }
        }
    }
}